import { Button, Center, Image } from '@chakra-ui/react'
import img1 from './assets/1.jpg'
import { useEffect, useState } from 'react'

const cardDeck = [
  0, 1, 2, 2, 3, 4, 4, 5, 6, 6, 7, 7, 8, 8, 9, 10, 11, 12, 13, 14, 14, 15, 15,
  15, 15, 16, 17, 18, 19, 19, 20, 20, 21, 21, 22, 22, 22,
]

var cards = JSON.parse(JSON.stringify(cardDeck))

const getImage = async () => {
  let retVal = -1
  let index = -1
  do {
    index = Math.floor(Math.random() * cards.length)
    const newVal = cards[index]
    if (newVal === 0) {
      cards = JSON.parse(JSON.stringify(cardDeck))
      console.log('shuffled')
    } else {
      retVal = newVal
    }
  } while (retVal === -1)

  cards.splice(index, 1)
  console.log(retVal, cards)

  return await import(`./assets/${retVal}.jpg`)
}

const Main = () => {
  const [img, setImg] = useState('')
  useEffect(() => {
    ;(async () => {
      setImg((await getImage())?.default)
    })().catch(console.error)
  }, [])

  const handleClick = () => {
    ;(async () => {
      setImg((await getImage())?.default)
    })().catch(console.error)
  }
  return (
    <>
      <Center onClick={handleClick}>
        {img ? <Image h='320px' src={img} /> : <Button>Shuffle</Button>}
      </Center>
    </>
  )
}

export default Main
